#matthew{

animation:zoomIn 1s;
}
.identity{
    overflow:hidden; 
    white-space:nowrap;
    margin: 0 auto;
    animation-delay:2s;
    animation: slideIn 5s ;

}

@keyframes zoomIn{
0%{
    transform:scale(30);
}
100%{
    transform:scale(1);
}

}
@keyframes slideIn{
  from{
    width:0%;
  }
  to{
    width:100%;
  }
}
